import RESTService from "./RESTService";
import {momentToDateTime} from "./utils";

export const updateOperationOrder = (formPayload, id, setReload) => {
  RESTService.getId("com.axelor.apps.production.db.OperationOrder", id).then(
      res => {
        res.json().then(result => {
          const { data } = result;
          if (data && data.length) {
            formPayload.version = data[0].version;
            RESTService.update(
                "com.axelor.apps.production.db.OperationOrder",
                formPayload,
                id
            ).then(res => {
              setReload(true)
            });
          }
        });
      }
  );
};

export const updateOperationOrderDateAndMachine = async (id, startDate, endDate, machineOrEmployeeId, reason, setReload, setReason, machinePlanning) => {

  setReason("");
  const { dhtmlx } = window;
  const formPayload = {
    plannedStartDateT: startDate,
    plannedEndDateT: endDate,
  };
  const operationOrderRes = await RESTService.getId(
      "com.axelor.apps.production.db.OperationOrder",
      id
  );
  const opeationOrderJson = await operationOrderRes.json();
  const { data: records } = opeationOrderJson;
  if (records && records.length) {
    formPayload.version = records[0].version;
    const operationOrderUpdateRes = await RESTService.update(
        "com.axelor.apps.production.db.OperationOrder",
        formPayload,
        id
    );
    const operationOrderUpdateJson = await operationOrderUpdateRes.json();
    const { status, data: updatedRecords } = operationOrderUpdateJson;
    if(status === -1) {
      dhtmlx.alert({
        text: `Une erreur est survenue: ${updatedRecords.message}`
      });
      return null;
    }
    if (updatedRecords && updatedRecords.length) {
      let data = {
        oldStartDate: momentToDateTime(records[0].plannedStartDateT),
        oldEndDate: momentToDateTime(records[0].plannedEndDateT),
        id,
        reason,
      };
      if(machinePlanning) {
        data.machineId = machineOrEmployeeId;
      }
      else{
        data.employeeId = machineOrEmployeeId;
      }
       RESTService.getAction(
          "com.axelor.apps.production.db.OperationOrder",
          "com.axelor.apps.sd.web.OperationOrderControllerSD:updateOperationOrderDateAndMachine",
          data
      ).then(() => {
        setReload(true);
      });
    }
  }



};

export const planOperationOrder = async (operationOrderId, machineOrEmployeId, workCenterId, startDate, machinePlanning) => {
  const { dhtmlx } = window;
  let formPayload = {};
  if(machinePlanning) {
    formPayload = {
      plannedStartDateT: startDate,
      machine: { id: machineOrEmployeId },
      workCenter: { id: workCenterId }
    };
  }
  else {
    formPayload = {
      plannedStartDateT: startDate,
      sdsEmployee: { id: machineOrEmployeId }
    };
  }

  const operationOrderRes = await RESTService.getId(
      "com.axelor.apps.production.db.OperationOrder",
      operationOrderId
  );
  const opeationOrderJson = await operationOrderRes.json();
  const { data: records } = opeationOrderJson;
  if (records && records.length) {
    formPayload.version = records[0].version;
    const operationOrderUpdateRes = await RESTService.update(
        "com.axelor.apps.production.db.OperationOrder",
        formPayload,
        operationOrderId
    );
    const operationOrderUpdateJson = await operationOrderUpdateRes.json();
    const { status, data: updatedRecords } = operationOrderUpdateJson;
    if(status === -1) {
      dhtmlx.alert({
        text: `Une erreur est survenue: ${updatedRecords.message}`
      });
      return null;
    }
    if (updatedRecords && updatedRecords.length) {
      const data = {
        context: {
          ...updatedRecords[0]
        }
      };
        return await RESTService.getAction(
            "com.axelor.apps.production.db.OperationOrder",
            "com.axelor.apps.production.pro.web.ProProductionOrderController:plan",
            data
        );
    }
  }
};