import React, { useEffect } from "react";

const withRenderOperation = WrappedComponent => {
  return props => {
    /**
     * Update the scheduler config with the machine list
     */
    useEffect(() => {
      const { scheduler } = window;
      scheduler.config.serverLists = {};
      if(props.planningEmployee && props.renderData.employeeList) {
        scheduler.serverList("sections", props.renderData.employeeList);
      }
      else {
        if(props.renderData.machineList) {
          scheduler.serverList("sections", props.renderData.machineList);
        }
        if(props.renderData.employeeList) {
          scheduler.serverList("sections", props.renderData.employeeList);
        }
      }

    }, [props.renderData.machineList, props.renderData.employeeList, props.planningEmployee]);



    if (!props.renderData.renderOperationOrder && !props.renderData.renderDraftOperationOrder) {
      return null;
    }

    return (
      <WrappedComponent
        {...props}
      />
    );
  };
};

export default withRenderOperation;
