import { translate, addYearsToDate } from "./utils";
import RESTService from "./RESTService";
import { updateOperationOrder, planOperationOrder } from "./AOSRESTService";

export const initFree = (setReload) => {
  const { scheduler } = window;
  //onclick of free button
  scheduler._click.buttons.free = id => {
    scheduler.hideQuickInfo();
    scheduler._quick_info_box = null;

    const formPayload = {
      isPinned: false
    };

    updateOperationOrder(formPayload, id, setReload);

    let icons = ["icon_freeze"];
    let event = scheduler.getEvent(id);
    if (event.statusSelect === 3) {
      icons.push("icon_start");
    }
    if (event.statusSelect === 4) {
      icons.push("icon_end");
    }
    icons.push("icon_delete");
    scheduler.config.icons_select = icons;
    scheduler.showQuickInfo(id);
  };
};

export const initStart = setReload => {
  const { scheduler, dhtmlx } = window;
  //onclick of start button
  scheduler._click.buttons.start = id => {
    const model = "com.axelor.apps.production.db.OperationOrder";
    const action = "action-operation-order-group-start";
    const data = {
      context: {
        id: id
      }
    };
    RESTService.getAction(model, action, data).then(res => {
      res.json().then(result => {
        const { data } = result;
        if (data && data.length) {
          const record = data[0];
          if (record && record.reload === true) {
            setReload(true);
          } else if (record && record.flash) {
            dhtmlx.alert({
              text: record.flash
            });
          }
        }
      });
    });
  };
};

export const initFreeze = (setReload) => {
  const { scheduler } = window;
  scheduler._click.buttons.freeze = id => {
    const formPayload = {
      isPinned: true
    };

    updateOperationOrder(formPayload, id, setReload);
  };
};

export const initChangeDuration = (setChangeDurationOpen, setChangeDurationId, setReason) => {
  const { scheduler } = window;
  scheduler._click.buttons.change_duration = id => {
    setChangeDurationOpen(true);
    setChangeDurationId(id);
    setReason("");
  };
};

export const initToPlan = setReload => {
  const { scheduler, dhtmlx } = window;
  //onclick of delete button
  scheduler._click.buttons.delete = id => {
    let event = scheduler.getEvent(id);
    if (event.isPinned) {
      dhtmlx.alert({
        text: "You can't unplan a frozen operation."
      });
    } else {
      dhtmlx.confirm({
        type: "confirm-warning",
        text: translate("Are you sure you want to unplan it?"),
        callback: result => {
          if (result) {
            RESTService.getAction(
              "com.axelor.apps.production.db.OperationOrder",
              "com.axelor.apps.production.pro.web.ProProductionOrderController:undoMicroPlanning",
              { id: id }
            ).then(res => {
              if (res) {
                setReload(true);
              }
            });
          }
        }
      });
    }
  };
};

export const initEnd = (setReload) => {
  const { scheduler } = window;
  //onclick of end button
  scheduler._click.buttons.end = id => {
    const model = "com.axelor.apps.production.db.OperationOrder";
    const action = "action-operation-order-group-finish";
    const data = {
      context: {
        id: id
      }
    };
    RESTService.getAction(model, action, data).then(res => {
      res.json().then(result => {
        setReload(true)
      });
    });
  };
};

export const drop = async (
  machineOrEmployeId,
  operationOrderId,
  workCenterId,
  startDate,
  setReload,
  machinePlanning
) => {
  const { dhtmlx } = window;
  const planResult = await planOperationOrder(
    operationOrderId,
    machineOrEmployeId,
    workCenterId,
    startDate,
    machinePlanning
  );
  if (planResult) {
    const planResultJson = await planResult.json();

    if (
      planResultJson &&
      planResultJson.data &&
      planResultJson.data[0] &&
      planResultJson.data[0].flash
    ) {
      dhtmlx.alert({
        text: translate(
          "Warning! You are about to drop an operation outside of the activity schedule of the machine."
        )
      });
    } else {
      setReload(true);
    }
  }
};

export const dragOver = async (context, machineList, setGreySpanList) => {
  const { scheduler } = window;
  const res = await RESTService.getAction(
    "com.axelor.apps.production.db.OperationOrder",
    `com.axelor.apps.production.pro.web.ProProductionOrderController:getAuthorizedMachineAndPreviousOperation`,
    context
  );
  const { data = {} } = await res.json();

  const previousOperation = data.lastOperationOrder;
  const authorizedMachineIds = data.machineIds;
  let endDate =  addYearsToDate(new Date(), 1);
  let startDate = addYearsToDate(new Date(), -1);
  if (machineList) {
    let timeSpanList = [];
    machineList.forEach(m => {
      if(authorizedMachineIds.includes(m.id)) {
        if(previousOperation) {
          let newEndDate = new Date(
              previousOperation.realEndDateT || previousOperation.plannedEndDateT
          );
          let newStartDate = addYearsToDate(new Date(
              previousOperation.realEndDateT || previousOperation.plannedEndDateT
          ), -1);
          const timeSpanId = scheduler.addMarkedTimespan({
            start_date: newStartDate,
            end_date: newEndDate,
            zones: "fullday",
            css: "gray_section",
            type: "previous_operation",
            sections: {
              timeline: m.id,
              week_timeline: m.id,
              month_timeline: m.id
            }
          });
          timeSpanList.push(timeSpanId);
        }
      }
      else {
        const timeSpanId = scheduler.addMarkedTimespan({
          start_date: startDate,
          end_date: endDate,
          zones: "fullday",
          css: "gray_section",
          type: "unauthorized_machine",
          sections: {
            timeline: m.id,
            week_timeline: m.id,
            month_timeline: m.id
          }
        });
        timeSpanList.push(timeSpanId);
      }

    });
    setGreySpanList(timeSpanList);
  }
  console.debug("updateView")
  scheduler.updateView();
};
