import React from 'react';

import { MenuItem, Button } from '@material-ui/core';
import MaterialSelect from '@material-ui/core/Select';
import CloseIcon from '@material-ui/icons/Close';
import styles from './style';
import * as PropTypes from 'prop-types';
import { css } from 'emotion';
import InputContainer from "../InputContainer/InputContainer";

class Select extends React.PureComponent {
  state = {
    open: false,
  };

  handleOpen = e => {
    e.preventDefault();
    this.setState({ open: !this.state.open });
  };

  handleClose = () => {
    this.setState({ open: false });
  };

  handleChange = event => {
    this.handleClose();
    this.props.onChange(event.target);
  };

  handleClear = event => {
    event.stopPropagation();
    this.props.onChange(null);
  };

  manageOptions = myOptions => {
    let { value: initialValue } = this.props;
    let value;
    let options = [];
    if (myOptions) {
      options = Object.keys(myOptions).map(id => {
        let option = myOptions[id];
        if ((option || {}).value === (initialValue || {}).value) {
          value = option.value;
        }
        return { label: (option || {}).label, value: option.value };
      });
    }

    return { options, value };
  };

  render() {
    let {
      value: initialValue,
      label,
      canRemove = true,
      errorMessage,
      errorMessages,
      name,
      clearButtonProps,
    } = this.props;
    let value;

    let myOptions = this.props.options;

    let { value: valueFromOption, options: managedOptions } = this.manageOptions(myOptions);
    value = valueFromOption;
    if (!value) {
      value = '';
      initialValue = null;
    }

    if (
      errorMessage == null &&
      errorMessages != null &&
      name != null &&
      errorMessages[name] != null
    ) {
      errorMessage = errorMessages[name];
    }

    return (
      <React.Fragment>
        <InputContainer
            label={label}
        >
          <div className={css(styles.selectContainer)}>
            <div className={css(styles.nativeSelectContainer)}>
              <MaterialSelect
                name={name}
                className={css(styles.select)}
                disabled={this.props.disabled}
                id={label}
                onChange={this.handleChange}
                onClose={this.handleClose}
                onOpen={this.handleOpen}
                open={this.state.open}
                value={value}
                readOnly={this.props.readOnly}
              >
                {managedOptions.map((option, i) => (
                  <MenuItem key={i} value={option.value}>
                    {option.label}
                  </MenuItem>
                ))}

                {managedOptions.length === 0 && (
                  <MenuItem key="noItemFounds" disabled>
                    No items found
                  </MenuItem>
                )}
              </MaterialSelect>
              <div className={css(styles.searchSelectButtonContainer)}>
                {!this.props.disabled && initialValue && canRemove && (
                  <span>
                    <Button
                      className={css(styles.clearButton)}
                      style={{ ...clearButtonProps }}
                      onClick={this.handleClear}
                      tabIndex={-1}
                    >
                      <CloseIcon fontSize={'inherit'} />
                    </Button>
                  </span>
                )}
              </div>
            </div>
          </div>
        </InputContainer>
      </React.Fragment>
    );
  }
}

Select.propTypes = {
  label: PropTypes.string.isRequired,
  showLabel: PropTypes.bool,
  canRemove: PropTypes.bool,
  required: PropTypes.bool,
};

Select.defaultProps = {
  showLabel: true,
  canRemove: true,
};

export default Select;
