import React, { useEffect, useState } from "react";
import ChangeDurationDialog from "../components/ChangeDurationDialog";
import withUrlParams from "../providers/UrlProvider";
import withMachine from "../providers/AOSProvider";
import withRenderOperation from "../providers/RenderProvider";
import { compose } from "recompose";
import { translate, isValidDate } from "../services/utils";
import LocalStorageService from "../services/LocalStorageService";
import Select from "react-select";
import {
  initSchedulerConfig,
  initCalendar,
  initDayTimeLine,
  initWeekTimeLine,
  initMonthTimeLine,
  onBeforeEventChange,
  attachEvents,
  initTemplates,
  initScheduler,
  addMarkedTimespan,
  addMaintenanceMarkedSpans,
  initPlanning
} from "../services/schedulerConfig";
import DraftOperationOrder from "./DraftOperationOrder";
import {
  initToPlan,
  initEnd,
  initFree,
  initFreeze,
  initChangeDuration,
  initStart,
  dragOver,
  drop
} from "../services/schedulerConfigRest";
import LoadingIndicator from "../components/LoadingIndicator";
import {updateOperationOrder} from "../services/AOSRESTService";

const planningTimeOptions = [
  { label: "5 min", name: "5", value: 5 },
  { label: "15 min", name: "15", value: 15 },
  { label: "30 min", name: "30", value: 30 },
  { label: "1h", name: "60", value: 60 },
  { label: "2h", name: "120", value: 120 },
  { label: "4h", name: "240", value: 240 },
  { label: "8h", name: "480", value: 480 }
];

function App(props) {
  const [spanList, setSpanList] = useState([]);
  const [detachEvents, setDetachEvents] = useState([]);
  const [eventChange, setEventChange] = useState(null);
  const [maintenanceSpanList, setMaintenanceSpanList] = useState([]);
  const [greySpanList, setGreySpanList] = useState([]);
  const [dragging, setDragging] = useState(false);
  const [calendar, setCalendar] = useState(null);
  const [currentMode, setCurrentMode] = useState(
    LocalStorageService.getActiveMode() || "timeline"
  );
  const [step, setStep] = useState(5);
  const [changeDurationOpen, setChangeDurationOpen] = useState(false);
  const [changeDurationId, setChangeDurationId] = useState(null);
  const [reason, setReason] = useState("");
  const [onMove, setOnMove] = useState({move: false, id: null});

  useEffect(
    () => {
      const { scheduler } = window;
      let activeStep = step || 5;
      let activeDate = new Date(scheduler.getState().date);
      initAll();
      initSchedulerConfig();
      initPlanning(props.type, props.planningViewType, props.renderData, props.workCenterGroupId, props.planningEmployee);

      if(isValidDate(activeDate)) {
        initDayTimeLine(props.type, activeStep, activeDate);
        initWeekTimeLine(props.type, activeStep, activeDate);
        initMonthTimeLine(props.type, activeStep, activeDate);
      }
      else {
        initDayTimeLine(props.type);
        initWeekTimeLine(props.type);
        initMonthTimeLine(props.type);
      }

      let events = attachEvents(setCurrentMode, props.renderData.manageChangeDuration, props.renderData.machineList, setGreySpanList, setOnMove);
      setDetachEvents(events);
      eventChange && scheduler.detachEvent(eventChange);
      let machinePlanning = props.renderData.machineList && props.renderData.machineList.length > 0;
      let event = onBeforeEventChange(reason, props.setReloadOperations, setReason, props.renderData.manageChangeDuration, setOnMove, machinePlanning);
      setEventChange(event);
      initTemplates(props.planningViewType);

      const spans = addMarkedTimespan(props.renderData.dayPlanningList, props.renderData.machineList);
      const maintenanceSpans = addMaintenanceMarkedSpans(
        props.maintenanceRequestList,
        maintenanceSpanList
      );

      setSpanList(spans);
      setMaintenanceSpanList(maintenanceSpans);
      initFreeze(props.setReloadOperations);
      initFree(props.setReloadOperations);
      initStart(props.setReloadOperations);
      initEnd(props.setReloadOperations);
      initToPlan(props.setReloadOperations);
      initScheduler(props.renderData.renderOperationOrder);
      initCalendar(calendar, setCalendar);

      if(props.renderData.manageChangeDuration) {
        initChangeDuration(setChangeDurationOpen, setChangeDurationId, setReason);
      }
    },
    // eslint-disable-next-line
    [props.renderData.renderOperationOrder, props.renderData.machineList, props.renderData.employeeList, props.renderData.manageChangeDuration]
  );

  useEffect(() => {
    const { scheduler } = window;
    if(reason) {
      eventChange && scheduler.detachEvent(eventChange);
      let machinePlanning = props.renderData.machineList && props.renderData.machineList.length > 0;
      let event = onBeforeEventChange(reason, props.setReloadOperations, setReason, props.renderData.manageChangeDuration, setOnMove, machinePlanning);
      setEventChange(event);
    }
    // eslint-disable-next-line
  }, [reason]);

  useEffect( () => {
    const { scheduler } = window;
    if(onMove && onMove.move && onMove.id) {
      const data = {
        context: {
          id: onMove.id
        }
      };
      dragOver(data, props.renderData.machineList, setGreySpanList);
    }
    if(onMove && !onMove.move && !onMove.id) {
      greySpanList.forEach(ts => {
        scheduler.deleteMarkedTimespan(ts);
      });
      setGreySpanList([]);
    }
  }, [onMove.move]);


  const onDragend = () => {
    const { scheduler } = window;
    setDragging(false);
    greySpanList && greySpanList.forEach(ts => {
      scheduler.deleteMarkedTimespan(ts);
    });
    setGreySpanList([]);
  };

  const onDragOver = e => {
    e.preventDefault();
    if (!dragging) {
      setDragging(true);
      console.debug("e.datatransfert", e.dataTransfer)
      let operationOrderId = LocalStorageService.getDragDataId();
      const data = {
        context: {
          id: operationOrderId
        }
      };
      dragOver(data, props.renderData.machineList, setGreySpanList);
    }
  };

  const onDrop = e => {
    const { scheduler, dhtmlx } = window;
    e.preventDefault();
    let action_data = scheduler.getActionData(e);
    const startDate = action_data.date;
    let endDate = new Date(startDate);
    endDate.setDate(endDate.getDate() + 1);
    const machineOrEmployeId = action_data.section;
    let workCenterId = null;
    let machinePlanning = false;
    if (props.renderData.machineList && props.renderData.machineList.length > 0) {
      const machine = props.renderData.machineList.find(
          m => m.id === machineOrEmployeId
      );
      if(machine) {
        machinePlanning = true;
        const workCenter = machine.workCenter;
        if (workCenter) {
          workCenterId = workCenter.id;
        }
      }
    }
    let eventId = scheduler.addEvent({
      start_date: startDate,
      end_date:   endDate,
      text:   "Test",
      section_id: machineOrEmployeId
    });
    if(scheduler.checkInMarkedTimespan(scheduler.getEvent(eventId), "previous_operation")) {
      scheduler.deleteEvent(eventId);
      greySpanList && greySpanList.forEach(ts => {
        scheduler.deleteMarkedTimespan(ts);
      });
      setGreySpanList([]);
      scheduler.updateView();
      dhtmlx.alert({
        text: translate(
            "Warning! You are about to reschedule an operation during a previous operation."
        )
      });

      return false;
    }
    else if(scheduler.checkInMarkedTimespan(scheduler.getEvent(eventId), "unauthorized_machine")) {
      scheduler.deleteEvent(eventId);
      greySpanList && greySpanList.forEach(ts => {
        scheduler.deleteMarkedTimespan(ts);
      });
      setGreySpanList([]);

      scheduler.updateView();
      dhtmlx.alert({
        text: translate(
            "Warning! You are about to reschedule an operation with an unauthorized machine."
        )
      });
      return false;
    }
    else {
      scheduler.deleteEvent(eventId);
      let operationOrderId = LocalStorageService.getDragDataId();
      if(workCenterId || !machinePlanning) {
        drop(machineOrEmployeId, operationOrderId, workCenterId, startDate, props.setReloadOperations, machinePlanning);
      }
      else {
        dhtmlx.alert({
          text: translate(
              "Warning ! The work center on the machine is not set."
          )
        });
      }

    }
  };

  const initAll = () => {
    const { scheduler } = window;
    //set last updates in local
    let mode = scheduler.getState().mode;
    let activeDate = scheduler.getState().date;
    LocalStorageService.setActiveDate(activeDate);
    LocalStorageService.setActiveMode(mode);

    //remove spans
    spanList &&
      spanList.forEach(span => {
        scheduler.deleteMarkedTimespan(span);
      });

    //remove maintenanceSpans
    maintenanceSpanList &&
      maintenanceSpanList.forEach(span => {
        scheduler.deleteMarkedTimespan(span);
      });

    //detachEvents
    detachEvents &&
      detachEvents.forEach(myEvent => {
        scheduler.detachEvent(myEvent);
      });

    greySpanList && greySpanList.forEach(ts => {
      scheduler.deleteMarkedTimespan(ts);
    });
    setGreySpanList([]);

    eventChange && scheduler.detachEvent(eventChange);

    //remove events
    scheduler.clearAll();
  };

  const refreshPage = () => {
    initAll();

    setMaintenanceSpanList([]);
    setSpanList([]);
    setDetachEvents([]);
    setEventChange(null);

    //initSchedulerConfig();
    props.setReload(true);
  };

  const onPlanningTimeChange = param => {
    setStep(param.value);
    initDayTimeLine(props.type, param.value);
  };

  const nextOnClick = () => {
    const { scheduler } = window;
    let state = scheduler.getState();
    let mode = state.mode;
    let activeDate = scheduler.getState().date;
    if (mode === "timeline") {
      let newDate = new Date(
        activeDate.setMinutes(activeDate.getMinutes() + step)
      );
      scheduler.setCurrentView(newDate);
      initDayTimeLine(props.type, step, newDate);
    } else if (mode === "week_timeline") {
      let newDate = activeDate.setDate(activeDate.getDate() + 7);
      scheduler.setCurrentView(newDate, "week_timeline");
    } else if (mode === "month_timeline") {
      let newDate = activeDate.setMonth(activeDate.getMonth() + 1);
      scheduler.setCurrentView(newDate, "month_timeline");
    }
  };

  const prevOnClick = () => {
    const { scheduler } = window;
    let state = scheduler.getState();
    let mode = state.mode;
    let activeDate = scheduler.getState().date;

    if (mode === "timeline") {
      let newDate = new Date(
        activeDate.setMinutes(activeDate.getMinutes() - step)
      );
      scheduler.setCurrentView(newDate);
      initDayTimeLine(props.type, step, newDate);
    } else if (mode === "week_timeline") {
      let newDate = activeDate.setDate(activeDate.getDate() - 7);
      scheduler.setCurrentView(newDate, "week_timeline");
    } else if (mode === "month_timeline") {
      let newDate = activeDate.setMonth(activeDate.getMonth() - 1);
      scheduler.setCurrentView(newDate, "month_timeline");
    }
  };

  const validateChangeDuration = () => {
    if(changeDurationId && reason) {
      const formPayload = {
        sdsChangeDuration: true
      };
      updateOperationOrder(formPayload, changeDurationId, props.setReloadOperations);
    }

  };
  return (
    <React.Fragment>
      <div style={{ display: "flex", flexDirection: "row" }}>
        <div
          style={{ float: "left", width: "250px", height: "380px", margin: 15 }}
        >
          {currentMode === "timeline" && (
            <Select
              className="basic-single"
              label={"Paramtrages"}
              classNamePrefix="select"
              defaultValue={planningTimeOptions[0]}
              name="planningTime"
              options={planningTimeOptions}
              onChange={onPlanningTimeChange}
            />
          )}

          {props.renderData.renderDraftOperationOrder && (
            <DraftOperationOrder
              operationOrderListLength={props.renderData.renderOperationOrder.length}
              draftOperationOrderListLength={
                props.renderData.renderDraftOperationOrder.length
              }
              onDragend={onDragend}
              workCenterGroup={props.workCenterGroup}
              setWorkCenterGroup={props.setWorkCenterGroup}
              filter={props.filter}
              setFilter={props.setFilter}
              secondFilter={props.secondFilter}
              setSecondFilter={props.setSecondFilter}
              renderDraftOperationOrder={props.renderData.renderDraftOperationOrder}
              workCenterGroupModelList={props.renderData.workCenterGroupModelList}
              partnerList={props.partnerList}
              productList={props.productList}
              searchTypeSelect={props.searchTypeSelect}
              setSearchTypeSelect={props.setSearchTypeSelect}
            />
          )}
          <div
            id="cal_here"
            style={{ width: "250px", height: "200px", marginTop: 50 }}
          ></div>
        </div>
        <div
          id="scheduler_here"
          className="dhx_cal_container"
          style={{ width: "100%", height: "900PX" }}
          onDrop={onDrop}
          onDragOver={onDragOver}
        >
          <div className="dhx_cal_navline">
            <div className="prevButton" onClick={prevOnClick}>
              &nbsp;
            </div>
            <div className="nextButton" onClick={nextOnClick}>
              &nbsp;
            </div>
            <div className="dhx_cal_today_button"></div>
            <div className="dhx_cal_refresh_button" onClick={refreshPage}>
              {translate("Refresh")}
            </div>
            <div className="dhx_cal_date"></div>
            <div className="dhx_cal_tab" name="timeline_tab"></div>
            <div className="dhx_cal_tab" name="week_timeline_tab"></div>
            <div className="dhx_cal_tab" name="month_timeline_tab"></div>
          </div>
          <div className="dhx_cal_header"></div>
          <div className="dhx_cal_data"></div>
        </div>
      </div>
      <ChangeDurationDialog open={changeDurationOpen} reason={reason} setReason={setReason} setChangeDurationOpen={setChangeDurationOpen} validateChangeDuration={validateChangeDuration}/>
      <LoadingIndicator loading={props.loading}/>
    </React.Fragment>
  );
}

export default compose(
  withUrlParams,
  withMachine,
  withRenderOperation
)(App);
