import React, {useEffect, useState} from "react";

const regexStockLocation = /[?&]stockLocationId=([^&#]*)/g; // ?stockLocationId=1
const regexMachineType = /[?&]machineTypeId=([^&#]*)/g; // ?machineTypeId=1
const regexMachine = /[?&]machineId=([^&#]*)/g; //?machineId=1
const regexWorkCenterGroup = /[?&]workCenterGroupId=([^&#]*)/g; //?workCenterGroupId=1
const regexEmployee = /[?&]employeeId=([^&#]*)/g; //?employeeId=1

//
const withUrlParams = (WrappedComponent) => {
    return () => {
        const [url] = useState(window.location.href);
        const [queryString] = useState(window.location.search);
        const [planningEmployee, setPlanningEmployee]  = useState(false);
        const [type, setType] = useState("days");
        const [planningViewType, setPlanningViewType] = useState("days");

        const [machineId, setMachineId] = useState(null);
        const [machineTypeId, setMachineTypeId] = useState(null);
        const [stockLocationId, setStockLocationId] = useState(null);
        const [workCenterGroupId, setWorkCenterGroupId] = useState(null);
        const [employeeId, setEmployeeId] = useState(null);

        useEffect( () => {
            const urlParams = new URLSearchParams(queryString);
            const planningEmployee = urlParams.get('planningEmployee');
            setPlanningEmployee(planningEmployee);
        }, [queryString]);

        useEffect(() => {
            const matchStockLocation = regexStockLocation.exec(url);
            const matchMachineType = regexMachineType.exec(url);
            const matchMachine = regexMachine.exec(url);
            const matchWorkCenterGroup = regexWorkCenterGroup.exec(url);
            const matchEmployee = regexEmployee.exec(url);

            if (matchStockLocation) {
                setStockLocationId(matchStockLocation[1]);
                setType("tree");
                setPlanningViewType("withStockLocation");
            }

            if (matchEmployee) {
                setEmployeeId(matchEmployee[1]);
                setType("days");
                setPlanningViewType("withEmployee");
            }

            //while machinetype is in URL
            if (matchMachineType) {
                setMachineTypeId(matchMachineType[1]);
                setType("tree");
                setPlanningViewType("withMachineType");
            }

            //while machine is in URL
            if (matchMachine) {
                setMachineId(matchMachine[1]);
                setType("days");
                setPlanningViewType("withMachine");
            }

            //while machine is in URL
            if (matchWorkCenterGroup) {
                setWorkCenterGroupId(matchWorkCenterGroup[1]);
                setType("tree");
                setPlanningViewType("withWorkCenterGroup");
            }
        }, [url]);

            if(!machineId && !machineTypeId && !stockLocationId && !workCenterGroupId && !employeeId) {
                return (
                  <div>
                    Les paramtres de l'url sont vides. <br /><br />Les paramtres attendus sont les suivants:<br /><br />
                    - ?stockLocationId=1<br/>
                    - ?machineId=1<br/>
                    - ?machineTypeId=1<br/>
                    - ?workCenterGroupId=1<br/>
                    - ?employeeId=1
                  </div>
                );
            }

            return <WrappedComponent url={url} type={type} planningEmployee={planningEmployee} planningViewType={planningViewType} employeeId={employeeId} workCenterGroupId={workCenterGroupId} stockLocationId={stockLocationId} machineTypeId={machineTypeId} machineId={machineId} />;
    };
};

export default withUrlParams;