import React, { useEffect, useState } from "react";
import RESTService from "../services/RESTService";
import { translate } from "../services/utils";
import DraftOperationOrder from "../app/DraftOperationOrder";

const withMachine = WrappedComponent => {
  return props => {
    const [renderData, setRenderData] = useState({
      machineList: null,
      employeeList: null,
      dayPlanningList: null,
      workCenterGroupId: props.workCenterGroupId,
      workCenterGroupModelList: null,
      renderOperationOrder: null,
      renderDraftOperationOrder: null,
      manageChangeDuration: false,
      workCenterGroupName: null
    });
    const [workCenterGroup, setWorkCenterGroup] = useState(null);
    const [filter, setFilter] = useState("");
    const [secondFilter, setSecondFilter] = useState("");
    const [reload, setReload] = useState(false);
    const [loading, setLoading] = useState(false);
    const [reloadOperations, setReloadOperations] = useState(false);
    const [searchTypeSelect, setSearchTypeSelect] = useState("left");

    const fetchData = async function() {
      setLoading(true);
      let machineList = [];
      let employeeList = [];
      let renderDraftOperationOrder = [];
      let renderOperationOrder = [];
      const res = await RESTService.getAction(
        "com.axelor.apps.production.db.OperationOrder",
        "com.axelor.apps.production.pro.web.PlanificationController:initData",
        {
          machineId: props.machineId,
          employeeId: props.employeeId,
          stockLocationId: props.stockLocationId,
          machineTypeId: props.machineTypeId,
          search: filter,
          secondSearch: secondFilter,
          searchTypeSelect,
          planningEmployee: props.planningEmployee || false,
          workCenterGroupId: props.workCenterGroupId || (workCenterGroup || {}).value
        }
      );
      const { data = {} } = await res.json();
      if (data && data.machineList) {
        machineList = data.machineList.sort((a, b) => a.id - b.id);
      }
      if(data && data.employeeList) {
        employeeList = data.employeeList.sort((a, b) => a.id - b.id);
      }
      if (data.renderOperationOrder) {
        renderDraftOperationOrder = data.renderOperationOrder.filter(
          oo => !oo.isMicroPlanningDone
        );
        renderOperationOrder = data.renderOperationOrder.filter(
          oo => oo.isMicroPlanningDone
        );
      }
      setRenderData({
        machineList,
        employeeList,
        dayPlanningList: data.dayPlanningList,
        workCenterGroupModelList: data.workCenterGroupModelList,
        renderOperationOrder,
        renderDraftOperationOrder,
        manageChangeDuration: data.manageChangeDuration,
        workCenterGroupId: props.workCenterGroupId || (workCenterGroup || {}).value,
        workCenterGroupName: data.workCenterGroupName
      });
      setReload(false);
    };

    /**
     * Almost static info, here we fetch data that won't change often, like machine, work center group model, planning etc..
     * @returns {Promise<void>}
     */
    const fetchStaticData = async function() {
      setLoading(true);
      let machineList = [];
      let employeeList = [];
      const res = await RESTService.getAction(
        "com.axelor.apps.production.db.OperationOrder",
        "com.axelor.apps.production.pro.web.PlanificationController:initStaticData",
        {
          machineId: props.machineId,
          employeeId: props.employeeId,
          stockLocationId: props.stockLocationId,
          machineTypeId: props.machineTypeId,
          workCenterGroupId: props.workCenterGroupId
        }
      );
      const { data = {} } = await res.json();
      if (data && data.machineList) {
        machineList = data.machineList.sort((a, b) => a.id - b.id);
      }
      if(data && data.employeeList) {
        employeeList = data.employeeList.sort((a, b) => a.id - b.id);
      }
      setRenderData({
        ...renderData,
        machineList,
        employeeList,
        dayPlanningList: data.dayPlanningList || [],
        workCenterGroupModelList: data.workCenterGroupModelList || [],
        manageChangeDuration: data.manageChangeDuration,
        workCenterGroupId: props.workCenterGroupId || (workCenterGroup || {}).value,
        workCenterGroupName: data.workCenterGroupName
      });
      setReload(false);
    };

    /**
     * fetch operation from machineList
     * @returns {Promise<void>}
     */
    const fetchOperationData = async function() {
      if ((renderData.machineList || renderData.employeeList) && !reload) {
        const machineIds = renderData.machineList.map(wc => wc.id);
        const employeeIds = renderData.employeeList.map(e => e.id);
        if ((machineIds && machineIds.length === 0) && employeeIds && employeeIds.length === 0) {
          const { dhtmlx } = window;
          if (props.machineTypeId) {
            dhtmlx.alert({
              text: translate(
                "Warning ! We cannot find any machine with this type of machine, or any machine that has a work center set."
              )
            });
          }
          if (props.machineId) {
            dhtmlx.alert({
              text: translate(
                "Warning ! The work center on the machine is not set."
              )
            });
          }
          if (props.employeeId) {
            dhtmlx.alert({
              text: translate(
                  "Warning ! The employee cannot be retrieve."
              )
            });
          }
          if (props.stockLocationId) {
            dhtmlx.alert({
              text: translate(
                "Warning ! We cannot find any machine or employee within this stock location, or any machine that has a work center set."
              )
            });
          }
          if (props.workCenterGroupId) {
            dhtmlx.alert({
              text: translate(
                  "Warning ! We cannot find any machine or employee for this work center group."
              )
            });
          }
        } else if (!props.planningEmployee && machineIds && machineIds.length > 0) {
          const res = await RESTService.getAction(
            "com.axelor.apps.production.db.OperationOrder",
            "com.axelor.apps.production.pro.web.PlanificationController:initOperationData",
            {
              machineIds: machineIds || [],
              employeeIds: null,
              search: filter,
              secondSearch: secondFilter,
              searchTypeSelect,
              planningEmployee: props.planningEmployee || false,
              workCenterGroupId: props.workCenterGroupId || (workCenterGroup || {}).value
            }
          );
          const { data = {} } = await res.json();
          let draftOperations = [];
          let operations = [];
          if (data.renderOperationOrder) {
            draftOperations = data.renderOperationOrder.filter(
              oo => !oo.isMicroPlanningDone
            );
            operations = data.renderOperationOrder.filter(
              oo => oo.isMicroPlanningDone
            );
          }
          setRenderData({
            ...renderData,
            renderOperationOrder: operations,
            renderDraftOperationOrder: draftOperations
          });
        }
        else if (employeeIds && employeeIds.length > 0) {
          const res = await RESTService.getAction(
              "com.axelor.apps.production.db.OperationOrder",
              "com.axelor.apps.production.pro.web.PlanificationController:initOperationData",
              {
                machineIds: null,
                employeeIds: employeeIds || [],
                search: filter,
                secondSearch: secondFilter,
                searchTypeSelect,
                planningEmployee: props.planningEmployee || false,
                workCenterGroupId: props.workCenterGroupId || (workCenterGroup || {}).value
              }
          );
          const { data = {} } = await res.json();
          let draftOperations = [];
          let operations = [];
          if (data.renderOperationOrder) {
            draftOperations = data.renderOperationOrder.filter(
                oo => !oo.isMicroPlanningDone
            );
            operations = data.renderOperationOrder.filter(
                oo => oo.isMicroPlanningDone
            );
          }
          setRenderData({
            ...renderData,
            renderOperationOrder: operations,
            renderDraftOperationOrder: draftOperations
          });
        }
      }
      setReload(false);
      setReloadOperations(false);
      setLoading(false);
    };

    useEffect(() => {
      if (!props.machineTypeId && !props.machineId && !props.stockLocationId && !props.workCenterGroupId && !props.employeeId) {
        return;
      }
      fetchStaticData();
      // eslint-disable-next-line
    }, [props.machineId, props.employeeId, props.machineTypeId, props.stockLocationId, props.workCenterGroupId]);

    useEffect(() => {
      if (!renderData.machineList && !renderData.employeeList) {
        return;
      }
      fetchOperationData();
      // eslint-disable-next-line
    }, [renderData.employeeList, renderData.machineList, filter, secondFilter, workCenterGroup, searchTypeSelect, props.planningEmployee]);

    useEffect(() => {
      if (reload) {
        fetchData();
      }
      // eslint-disable-next-line
    }, [reload]);

    useEffect(() => {
      if (reloadOperations) {
        fetchOperationData();
      }
      // eslint-disable-next-line
    }, [reloadOperations]);

    if (
      !renderData &&
      !renderData.renderOperationOrder &&
      !renderData.machineList &&
      !renderData.employeeList &&
      !renderData.workCenterGroupModelList &&
      !renderData.renderDraftOperationOrder &&
      !renderData.dayPlanningList &&
      !props.machineTypeId &&
      !props.machineId &&
      !props.employeeId &&
      !props.stockLocationId &&
      !props.workCenterGroupId
    ) {
      return null;
    }
    return (
      <WrappedComponent
        renderData={renderData}
        setReload={setReload}
        setReloadOperations={setReloadOperations}
        workCenterGroup={workCenterGroup}
        workCenterGroupId={props.workCenterGroupId}
        setWorkCenterGroup={setWorkCenterGroup}
        filter={filter}
        setFilter={setFilter}
        secondFilter={secondFilter}
        setSecondFilter={setSecondFilter}
        loading={loading}
        searchTypeSelect={searchTypeSelect}
        setSearchTypeSelect={setSearchTypeSelect}
        planningEmployee={props.planningEmployee}
        {...props}
      />
    );
  };
};

export default withMachine;
