import {isEmpty} from "./utils";

class LocalStorageService {
  static OPERATION_ORDER_EVENT_COLOR_STORE_KEY =
    "OPERATION_ORDER_EVENT_COLOR_STORE_KEY";
  static OPEN_STOCK_LOCATION_STORE_KEY = "OPEN_STOCK_LOCATION_STORE_KEY";
  static OPEN_MACHINE_TYPE_STORE_KEY = "OPEN_MACHINE_TYPE_STORE_KEY";
  static LAST_ACTIVE_MODE = "LAST_ACTIVE_MODE";
  static LAST_ACTIVE_DATE = "LAST_ACTIVE_DATE";
  static DATA_ID_BEING_DRAG = "DATA_ID_BEING_DRAG";


  static setDragDataId(id = null) {
    window.localStorage.setItem(LocalStorageService.DATA_ID_BEING_DRAG, id);
  }

  static getDragDataId() {
    return window.localStorage.getItem(LocalStorageService.DATA_ID_BEING_DRAG);
  }

  static setActiveMode(data = null) {
    window.localStorage.setItem(
      LocalStorageService.LAST_ACTIVE_MODE,
      JSON.stringify(data)
    );
  }

  static getActiveMode() {
    try {
      const data = window.localStorage.getItem(
        LocalStorageService.LAST_ACTIVE_MODE
      );
      let jsonData = {};
      if (data) {
        jsonData = JSON.parse(data);
      }
      if(isEmpty(jsonData)) { return null}
      return jsonData;
    } catch (e) {}
    return null;
  }

  static setActiveDate(data = null) {
    window.localStorage.setItem(
      LocalStorageService.LAST_ACTIVE_DATE,
      JSON.stringify(data)
    );
  }

  static getActiveDate() {
    try {
      const data = window.localStorage.getItem(
        LocalStorageService.LAST_ACTIVE_DATE
      );
      let jsonData = {};
      if (data) {
        jsonData = JSON.parse(data);
      }

      if(isEmpty(jsonData)) { return null}
      return jsonData;
    } catch (e) {}
    return null;
  }

  static getOperationOrders() {
    try {
      const item = window.localStorage.getItem(
        LocalStorageService.OPERATION_ORDER_EVENT_COLOR_STORE_KEY
      );
      return item ? JSON.parse(item) : [];
    } catch (e) {
      return [];
    }
  };

  static setOperationOrders(data = null) {
    window.localStorage.setItem(
      LocalStorageService.OPERATION_ORDER_EVENT_COLOR_STORE_KEY,
      JSON.stringify(data)
    );
  }

  static getOpenStockLocations() {
    try {
      const item = window.localStorage.getItem(
        LocalStorageService.OPEN_STOCK_LOCATION_STORE_KEY
      );
      return item ? JSON.parse(item) : [];
    } catch (e) {
      return [];
    }
  }

  static setOpenStockLocations(data = null) {
    window.localStorage.setItem(
      LocalStorageService.OPEN_STOCK_LOCATION_STORE_KEY,
      JSON.stringify(data)
    );
  }

  static getOpenMachineTypes() {
    try {
      const item = window.localStorage.getItem(
        LocalStorageService.OPEN_MACHINE_TYPE_STORE_KEY
      );
      return item ? JSON.parse(item) : [];
    } catch (e) {
      return [];
    }
  }

  static setOpenMachineTypes(data = null) {
    window.localStorage.setItem(
      LocalStorageService.OPEN_MACHINE_TYPE_STORE_KEY,
      JSON.stringify(data)
    );
  }
}

export default LocalStorageService;
