export const initCalendar = (calendar, setCalendar) => {
  const { scheduler } = window;
  if (calendar) {
  } else {
    scheduler.destroyCalendar(calendar);
    let newCalendar = scheduler.renderCalendar({
      container: "cal_here",
      navigation: true,
      handler: function(date) {
        scheduler.setCurrentView(date, scheduler._mode);
      }
    });
    setCalendar(newCalendar);
    scheduler.linkCalendar(newCalendar);
  }
};

export const initDateFormat = () => {
  const { scheduler } = window;
  let weekDateFormat = scheduler.date.date_to_str("%M %j");

  scheduler.templates.day_date = function(date) {
    return formatDateWithoutTime(date);
  };

  scheduler.templates.week_timeline_scale_date = date => {
    return "<br/>" + weekDateFormat(date);
  };

  scheduler.templates.month_timeline_scale_date = date => {
    return getShortDayName(date) + "<br/>" + date.getDate();
  };

  scheduler.calculateDuration = event => {
    return Math.round((event.end_date - event.start_date) / (1000 * 60 * 60));
  };
};

export const initScheduler = renderOperationOrder => {
  const { scheduler } = window;
  let activeMode = LocalStorageService.getActiveMode() || "timeline";
  let activeDate = LocalStorageService.getActiveDate() || new Date();
  scheduler.init("scheduler_here", activeDate, activeMode);
  scheduler.parse(renderOperationOrder, "json");
};

export const initDayTimeLine = (type, step, date) => {
  const { scheduler } = window;
  scheduler.config.serverLists["timeline"] = "sections";
  const todayDate = date || new Date();
  const hours = todayDate.getHours();
  const minutes = todayDate.getMinutes() - (todayDate.getMinutes() % 5);
  const currentStep = step || 5;
  const totalMinutes = (hours * 60 + minutes - 10) / currentStep;
  scheduler.createTimelineView({
    fit_events: true,
    name: "timeline",
    x_unit: "minute",
    x_date: "%H:%i",
    x_step: currentStep,
    x_size: 16,
    x_start: totalMinutes,
    x_length: 14,
    render: type,
    scrollable: true,
    column_width: 70,
    smart_rendering: true,
    folder_dy: 40,
    full_event__dy: true,
    event_dy: 100,
    dy: 80,
    y_unit: scheduler.serverList("sections"),
    y_property: "section_id",
    section_autoheight: false,
    resize_events: false
  });
};

