export const initPlanning = (type, planningViewType, renderData, workCenterGroupId, planningEmployee) => {
  console.debug("planningEmployee", planningEmployee)
  if (!renderData.machineList && !renderData.employeeList) {
    return;
  }
  const { scheduler } = window;

  //after init
  scheduler.initPlanning = () => {
    //parent is stock location
    if (type === "tree" && planningViewType === "withStockLocation") {
      let parent = {};
      if(planningEmployee) {
        if (renderData.employeeList) {
          renderData.employeeList.forEach(e => {
            const stockLocation = Object.assign(
                { employees: [] },
                parent[e.sdsWorkshop.id] || e.sdsWorkshop
            );
            stockLocation.employees.push(e);
            parent[stockLocation.id] = stockLocation;
          });
        }
      }
      else if (renderData.machineList) {
        renderData.machineList.forEach(machine => {
          const stockLocation = Object.assign(
            { machines: [] },
            parent[machine.stockLocation.id] || machine.stockLocation
          );
          stockLocation.machines.push(machine);
          parent[stockLocation.id] = stockLocation;
        });
      }
      createTree(parent, scheduler, "withStockLocation");
    }
    else if (type === "tree" && planningViewType === "withWorkCenterGroup") {
      let parent = {};
      if (renderData.machineList) {
        renderData.machineList.forEach(machine => {
          const workCenterGroup = Object.assign(
              { machines: [] },
              parent[workCenterGroupId] || {id: workCenterGroupId, name: renderData.workCenterGroupName}
          );
          workCenterGroup.machines.push(machine);
          parent[workCenterGroup.id] = workCenterGroup;
        });
      }
      if (renderData.employeeList) {
        renderData.employeeList.forEach(e => {
          const workCenterGroup = Object.assign(
              { employees: [] },
              parent[workCenterGroupId] || {id: workCenterGroupId, name: renderData.workCenterGroupName}
          );
          workCenterGroup.employees.push(e);
          parent[workCenterGroup.id] = workCenterGroup;
        });
      }
      createTree(parent, scheduler, "withWorkCenterGroup");
    } else if (type === "tree" && planningViewType === "withMachineType") {
      //parent is machine type
      let parent = {};
      if (renderData.machineList) {
        renderData.machineList.forEach(machine => {
          const machineType = Object.assign(
            { machines: [] },
            parent[machine.machineType.id] || machine.machineType
          );
          machineType.machines.push(machine);
          parent[machineType.id] = machineType;
        });
      }
      createTree(parent, scheduler, "withMachineType");
    } else {
      //machine day view
      if(renderData.machineList && renderData.machineList.length > 0) {
        renderData.machineList.forEach(element => {
          element.key = element.id;
          element.label = `${element.name} - ${element.serialNumber}`;
          element.section_id = element.id;
        });
        scheduler.serverList("sections", renderData.machineList);
      }
      else if(renderData.employeeList && renderData.employeeList.length > 0) {
        renderData.employeeList.forEach(element => {
          element.key = element.id;
          element.label = `${element.name} `;
          element.section_id = element.id;
        });
        scheduler.serverList("sections", renderData.employeeList);
      }

    }
  };

  scheduler.initPlanning();
};

export const updateParent = (parent, scheduler) => {
  scheduler.serverList("sections", parent.slice());
};

export const setLocalOpenStockLocations = (parent, scheduler) => {
  let localOpenStockLocations = LocalStorageService.getOpenStockLocations();
  let openStockLocations = Object.keys(localOpenStockLocations).map(
    i => localOpenStockLocations[i]
  );

  parent.forEach(element => {
    let foundStockLocation = localOpenStockLocations[element.id];
    if (!foundStockLocation) {
      openStockLocations.push({ id: element.id, open: false });
    } else {
      element.open = foundStockLocation.open;
    }
  });
  updateParent(parent, scheduler);
  LocalStorageService.setOpenStockLocations(arrayToObject(openStockLocations));
};

export const setLocalOpenMachineTypes = (parent, scheduler) => {
  let localMachineTypes = LocalStorageService.getOpenMachineTypes();
  let openMachineTypes = Object.keys(localMachineTypes).map(
    i => localMachineTypes[i]
  );

  parent.forEach(element => {
    let foundMachineType = localMachineTypes[element.id];
    if (!foundMachineType) {
      openMachineTypes.push({ id: element.id, open: false });
    } else {
      element.open = foundMachineType.open;
    }
  });
  updateParent(parent, scheduler);
  LocalStorageService.setOpenMachineTypes(arrayToObject(openMachineTypes));
};

export const createTree = (parent, scheduler, planningViewType) => {
  parent = Object.values(parent).map(cust => {
    let o = {
      id: cust.id,
      key: cust.name,
      label: cust.name
    };
    if(cust.employees) {
      o.children = Object.values(cust.employees || {}).map(type => {
        return {
          ...cust,
          ...type,
          label: `${type.name}`,
          key: type.id
        };
      })
    }
    if(cust.machines) {
      o.children = Object.values(cust.machines || {}).map(type => {
        return {
          ...cust,
          ...type,
          label: `${type.name} - ${type.serialNumber}`,
          key: type.id
        };
      })
    }
    return o;
  });
  if (planningViewType === "withStockLocation") {
    setLocalOpenStockLocations(parent, scheduler);
  } else {
    setLocalOpenMachineTypes(parent, scheduler);
  }
};

export const addMarkedTimespan = (dayPlanningList, machineList) => {
  if (!machineList) {
    return;
  }
  const { scheduler } = window;
  const week = [
    "sunday",
    "monday",
    "tuesday",
    "wednesday",
    "thursday",
    "friday",
    "saturday"
  ];
  let spans = [],
    zones;
  machineList &&
    machineList.forEach(mac => {
      dayPlanningList &&
        dayPlanningList.forEach(weekday => {
          if (
            mac.weeklyPlanning &&
            mac.weeklyPlanning.id === weekday.weeklyPlann.id
          ) {
            const {
              name,
              morningFrom,
              morningTo,
              afternoonFrom,
              afternoonTo
            } = weekday;

            let morningFromZone,
              morningToZone,
              afternoonFromZone,
              afternoonToZone,
              invert_zones = true;
            morningFromZone = getCorrectTimeFormat(morningFrom);
            morningToZone = getCorrectTimeFormat(morningTo);
            afternoonFromZone = getCorrectTimeFormat(afternoonFrom);
            afternoonToZone = getCorrectTimeFormat(afternoonTo);

            if (morningFrom && morningTo && afternoonFrom && afternoonTo) {
              zones = [
                morningFromZone,
                morningToZone,
                afternoonFromZone,
                afternoonToZone
              ];
            } else if (
              morningFrom &&
              morningTo &&
              afternoonFrom === null &&
              afternoonTo === null
            ) {
              zones = [morningFromZone, morningToZone];
            } else if (
              morningFrom &&
              morningTo === null &&
              afternoonFrom === null &&
              afternoonTo
            ) {
              zones = [morningFromZone, afternoonToZone];
            } else if (
              morningFrom &&
              morningTo === null &&
              afternoonTo === null
            ) {
              zones = [morningFromZone, 24 * 60];
            } else if (
              morningFrom === null &&
              morningTo === null &&
              afternoonFrom &&
              afternoonTo
            ) {
              zones = [afternoonFromZone, afternoonToZone];
            } else if (
              morningFrom === null &&
              morningTo === null &&
              afternoonFrom &&
              afternoonTo === null
            ) {
              zones = [afternoonFromZone, 24 * 60];
            } else if (
              morningFrom === null &&
              afternoonFrom === null &&
              morningTo
            ) {
              zones = [0, morningToZone];
            } else if (
              morningFrom === null &&
              morningTo === null &&
              afternoonFrom === null &&
              afternoonTo
            ) {
              zones = [0, afternoonToZone];
            } else if (
              morningFrom === null &&
              morningTo &&
              afternoonFrom &&
              afternoonTo === null
            ) {
              zones = [0, morningToZone, afternoonFromZone, 24 * 60];
            } else if (
              morningFrom === null &&
              morningTo === null &&
              afternoonFrom === null &&
              afternoonTo === null
            ) {
              zones = "fullday";
              invert_zones = false;
            }

            //Create planning marks
            let spanId = scheduler.addMarkedTimespan({
              days: week.indexOf(name),
              zones: zones,
              invert_zones: invert_zones,
              css: "gray_section",
              sections: {
                timeline: mac.id,
                week_timeline: mac.id,
                month_timeline: mac.id
              },
              type: "planning"
            });
            spans.push(spanId);
          }
        });
    });
  return spans;
};

function showAlert(isMaintenanceSpan, isPlanningSpan) {
  const { dhtmlx } = window;
  if (isMaintenanceSpan) {
    dhtmlx.alert({
      text: translate(
        "Warning ! The machine is under maintenance during this period."
      )
    });
  } else {
    if (isPlanningSpan) {
      dhtmlx.alert({
        text: translate(
          "Warning! You are about to drop an operation outside of the activity schedule of the machine."
        )
      });
    }
  }
}

export const onBeforeEventChange = (
  reason,
  setReload,
  setReason,
  manageChangeDuration,
  setOnMove,
  machinePlanning
) => {
  //event drag & drop
  const { scheduler, dhtmlx } = window;
  return scheduler.attachEvent("onBeforeEventChanged", async function(
    ev,
    e,
    is_new,
    original
  ) {
    setOnMove({move: null, id: null});
    if(scheduler.checkInMarkedTimespan(scheduler.getEvent(ev.id), "previous_operation")) {
      dhtmlx.alert({
        text: translate(
            "Warning! You are about to reschedule an operation during a previous operation."
        )
      });
      scheduler.getEvent(ev.id).start_date = original.start_date;
      scheduler.getEvent(ev.id).end_date = original.end_date;
      scheduler.getEvent(ev.id).section_id = original.section_id;
      scheduler.updateEvent(ev.id);
      return false;
    }
    else if(scheduler.checkInMarkedTimespan(scheduler.getEvent(ev.id), "unauthorized_machine")) {
      dhtmlx.alert({
        text: translate(
            "Warning! You are about to reschedule an operation with an unauthorized machine."
        )
      });
      scheduler.getEvent(ev.id).start_date = original.start_date;
      scheduler.getEvent(ev.id).end_date = original.end_date;
      scheduler.getEvent(ev.id).section_id = original.section_id;
      scheduler.updateEvent(ev.id);
      return false;
    }
    //maintenace operation order can't change machine
    if (original.isMaintained === true) {
      if (ev.section_id !== original.section_id) {
        return false;
      } else {
        return true;
      }
    } else {
      if (ev.id) {
        if (
          ev.section_id !== original.section_id ||
          (ev.start_date.getTime() !== original.start_date.getTime() ||
            ev.end_date.getTime() !== original.end_date.getTime())
        ) {
          if (manageChangeDuration) {
            updateOperationOrderDateAndMachine(
              ev.id,
              ev.start_date,
              ev.end_date,
              ev.section_id,
              reason,
              setReload,
              setReason,
              machinePlanning
            );
          } else {
            const formPayload = {
              plannedStartDateT: ev.start_date,
              plannedEndDateT: ev.end_date
            };

            if (ev.isMaintained === false) {
              if(machinePlanning) {
                formPayload.machine = { id: ev.section_id };
              }
              else {
                formPayload.sdsEmployee = { id: ev.section_id };
              }
            }
            updateOperationOrder(formPayload, ev.id, setReload);
          }
          return true;
        } else {
          return false;
        }
      }
    }
  });
};

export const attachEvents = (setCurrentMode, manageChangeDuration, machineList, setGreySpanList, setOnMove) => {
  const detachEvents = [];
  const { scheduler } = window;

  let onSchedulerReady = scheduler.attachEvent("onSchedulerReady", function() {
    var to_str = scheduler.date.date_to_str("%H:%i");
    scheduler.templates.hour_scale = function(date) {
      return to_str(date);
    };
  });

  detachEvents.push(onSchedulerReady);

  let onEventDrag = scheduler.attachEvent("onEventDrag", function (id, mode, e){
    //any custom logic here
    if(mode === "move") {
        setOnMove({move: true, id});
    }
  });
  detachEvents.push(onEventDrag);


  let onViewChange = scheduler.attachEvent("onViewChange", function(mode, date) {
    setCurrentMode(mode);
  });
  detachEvents.push(onViewChange);

  let onBeforeViewChange = scheduler.attachEvent(
    "onBeforeViewChange",
    (old_mode, old_date, mode, currentDate) => {
      let date = moment(currentDate).toDate();
      if (mode === "month_timeline") {
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let d = new Date(year, month, 0);
        let days = d.getDate();
        scheduler.matrix["month_timeline"].x_size = days;
        scheduler.matrix["month_timeline"].x_length = days;
      }
      return true;
    }
  );
  detachEvents.push(onBeforeViewChange);

  let onBeforeLightbox = scheduler.attachEvent("onBeforeLightbox", id => {
    return false;
  });
  detachEvents.push(onBeforeLightbox);

  let onBeforeEventCreated = scheduler.attachEvent(
    "onBeforeEventCreated",
    e => {
      return false;
    }
  );
  detachEvents.push(onBeforeEventCreated);

  let onSchedulerResize = scheduler.attachEvent("onSchedulerResize", () => {
    scheduler.setCurrentView();
  });
  detachEvents.push(onSchedulerResize);

  //Freeze events
  let onBeforeDrag = scheduler.attachEvent("onBeforeDrag", (id, mode, ev) => {
    let event = scheduler.getEvent(id);
    if (mode === "resize" && !event.changeDuration) {
      return false;
    }
    if (
      event &&
      (event.isPinned === true ||
        event.statusSelect === 6 ||
        event.statusSelect === 4)
    ) {
      return false;
    }
    return true;
  });
  detachEvents.push(onBeforeDrag);

  //dynamic scale changes
  let onClick = scheduler.attachEvent("onClick", id => {
    let event = scheduler.getEvent(id);
    hideQuickInfo(scheduler);
    let icons = [];
    if (!event.isMaintained) {
      if (event.statusSelect === 3) {
        if (event.isPinned === true) {
          icons.push("icon_free");
        } else {
          icons.push("icon_freeze");
        }
        if (event.statusSelect === 3) {
          icons.push("icon_start");
        }
      }

      if (event.statusSelect === 4) {
        icons.push("icon_end");
      }
      if (event.statusSelect !== 4 && event.statusSelect !== 6) {
        icons.push("icon_delete");
      }
      if (
        manageChangeDuration &&
        event &&
        (event.isPinned === false &&
          !event.changeDuration &&
          (event.statusSelect !== 6 && event.statusSelect !== 4))
      ) {
        icons.push("icon_change_duration");
      }
    }

    scheduler.config.icons_select = icons;
    scheduler.showQuickInfo(id);
    return true;
  });
  detachEvents.push(onClick);

  let onDragEnd = scheduler.attachEvent("onDragEnd", (id, mode, e) => {
    let event = scheduler.getEvent(id);
    let isMaintenanceSpan = scheduler.checkInMarkedTimespan(
      event,
      "maintenance"
    );
    let isPlanningSpan = scheduler.checkInMarkedTimespan(event, "planning");

    showAlert(isMaintenanceSpan, isPlanningSpan);
  });
  detachEvents.push(onDragEnd);

  /**
   * ev	object	the event object
   * evs	array	a collection of events' objects which already occupy the same time-slot
   * @type {string | * | void}
   */
  let onEventCollision = scheduler.attachEvent("onEventCollision", function(
    ev,
    evs
  ) {
    let canProceed = true;
    evs.forEach(event => {
      let isBlockedSpan = scheduler.checkInMarkedTimespan(event, "blocked");
      let isMaintenanceSpan = scheduler.checkInMarkedTimespan(
        event,
        "maintenance"
      );
      let isPlanningSpan = scheduler.checkInMarkedTimespan(event, "planning");
      if (isBlockedSpan) {
        canProceed = false;
      }
      showAlert(isMaintenanceSpan, isPlanningSpan);
    });
    return canProceed;
  });

  detachEvents.push(onEventCollision);

  return detachEvents;
};

/**
 * maintenance marked spans on scheduler (red)
 */
export const addMaintenanceMarkedSpans = (
  maintenanceRequestList,
  maintenanceSpanList,
  equipmentMaintenanceList
) => {
  const { scheduler } = window;
  const maintenanceSpans = [...maintenanceSpanList];
  maintenanceRequestList &&
    maintenanceRequestList.forEach(request => {
      let {
        actionSelect,
        requestDate,
        doneOn,
        expectedDate,
        equipementMaintenance
      } = request;
      let getSection =
        equipmentMaintenanceList &&
        equipmentMaintenanceList.find(
          equipment => equipment.id === equipementMaintenance.id
        );
      const id = getSection && getSection.machine && getSection.machine.id;

      if (id) {
        if (actionSelect === 1) {
          let config = {
            days: moment(doneOn || expectedDate).toDate(),
            zones: "fullday",
            css: "red_section",
            sections: { timeline: id, week_timeline: id, month_timeline: id },
            type: "maintenance"
          };
          scheduler.deleteMarkedTimespan(config);
          scheduler.addMarkedTimespan(config);
          maintenanceSpanList.push(config);
        } else {
          let dates = enumerateDaysBetweenDates(
            moment(requestDate).toDate(),
            moment(doneOn || expectedDate).toDate()
          );
          let config = {
            days: dates,
            zones: "fullday",
            css: "red_section",
            sections: { timeline: id, week_timeline: id, month_timeline: id },
            type: "maintenance"
          };
          scheduler.deleteMarkedTimespan(config);
          scheduler.addMarkedTimespan(config);
          maintenanceSpans.push(config);
        }
      }
    });
  return maintenanceSpans;
};

const getCss = event => {
  let css = "";
  let prioritySelect;

  if (event.manufOrder) {
    prioritySelect = event.manufOrder.prioritySelect;
  }

  if (event.isMaintained === true && event.isPinned === false) {
    css += "event_maintenance";
  } else if (event.isMaintained === true && event.isPinned === true) {
    css += "event_maintenance_freeze";
  } else if (
    prioritySelect === 4 &&
    event.isPinned === false &&
    event.isMaintained === false
  ) {
    css += "event_danger";
  } else if (
    prioritySelect === 3 &&
    event.isPinned === false &&
    event.isMaintained === false
  ) {
    css += "event_warning";
  } else if (
    prioritySelect === 2 &&
    event.isPinned === false &&
    event.isMaintained === false
  ) {
    css += "event_primary";
  } else if (
    prioritySelect === 1 &&
    event.isPinned === false &&
    event.isMaintained === false
  ) {
    css += "event_normal";
  } else if (
    prioritySelect === 4 &&
    event.isPinned === true &&
    event.isMaintained === false
  ) {
    css += "event_danger_freeze";
  } else if (
    prioritySelect === 3 &&
    event.isPinned === true &&
    event.isMaintained === false
  ) {
    css += "event_warning_freeze";
  } else if (
    prioritySelect === 2 &&
    event.isPinned === true &&
    event.isMaintained === false
  ) {
    css += "event_primary_freeze";
  } else if (
    prioritySelect === 1 &&
    event.isPinned === true &&
    event.isMaintained === false
  ) {
    css += "event_normal_freeze";
  } else if (prioritySelect === null) {
    css += "event_usual";
  }

  if (
    event.statusSelect === 4 &&
    event.isMaintained === false &&
    event.saleOrder &&
    event.saleOrder.sdsTypeSelect === 1
  ) {
    css += " event_in_progress_posed";
  } else if (
    event.statusSelect === 4 &&
    event.isMaintained === false &&
    event.saleOrder &&
    event.saleOrder.sdsTypeSelect === 2
  ) {
    css += " event_in_progress_not_posed";
  } else if (event.statusSelect === 4 && event.isMaintained === false) {
    css += " event_in_progress";
  }

  if (event.statusSelect === 6 && event.isMaintained === false) {
    css += " event_finished";
  }

  if (
    event.saleOrder &&
    event.saleOrder.sdsTypeSelect === 2 &&
    event.statusSelect !== 6 &&
    event.statusSelect !== 4
  ) {
    css += " event_saleOrder_not_posed";
  }

  if (
    event.saleOrder &&
    event.saleOrder.sdsTypeSelect === 1 &&
    event.statusSelect !== 6 &&
    event.statusSelect !== 4
  ) {
    css += " event_saleOrder_posed";
  }

  return css;
};

export const label_pattern = (start, end, event, type) => {
  let lines = [];
  if (event) {
    /**
     * Header: Manuf order sequence and priority
     */
    if (type === "event") {
      let name = "";
      if (event.product && event.product.fullName && event.product.code) {
        name = event.product.fullName.replace(`[${event.product.code}]`, "");
      }
      let css = getCss(event);
      lines.push(`
        <div className="container" style="height: 100%; width: 100%">
          <div className="header" style="margin-left: -0.75%; background-color: ${
            event.headerColor
          }">
            ${event.manufOrder && event.manufOrder.manufOrderSeq}
            <div style="position: absolute; top: 0; right: 2px;">
              ${event.materialCode ? event.materialCode.sdsCode : ""}
            </div>
          </div>
          <div class="dhx_cal_event_line ${css}" className="mainbody" style=" z-index=-100000!important; height: 100%; background-color: ${
        event.contentColor
      }; color: black">
            ${event.operationName}
            <br />
            ${
              event.product && event.product.code
                ? `[${event.product.code}]`
                : ""
            }
            <br />
            ${name}
            <br />
            ${
              event.clientPartner && event.clientPartner.fullName
                ? event.clientPartner.fullName
                : ""
            }
          </div>
        </div>
      `);

      /*let content = `<div style=" margin-left: -0.75%; background-color: ${event.contentColor}">
                        <div class="ellipsive_content">
                            ${event.operationName}
                            <br />
                            ${event.product && event.product.code ? `[${event.product.code}]` : ""}
                            ${event.product && event.product.code ? `[${event.product.code}]` : ""}
                            <br />
                            ${name}
                            <br />
                            ${event.clientPartner && event.clientPartner.fullName ? event.clientPartner.fullName : "" }
                            <br />
                        </div>
                    </div>`;
      lines.push(content);*/
    }

    if (type === "tooltip") {
      lines.push(`<b>
      <div >
        <div  style="margin-top: 0%; margin-left: 1%;">
          ${event.manufOrder && event.manufOrder.manufOrderSeq}
          <div style="position: absolute; top: 0; right: 2px;">${
            event.materialCode ? event.materialCode.sdsCode : ""
          }</div>
        </div>
      </div>
    </b>`);
      if (event.isMaintained === false) {
        if (event.product) {
          lines.push(event.product && event.product.fullName);
        }
      }
      if (event.clientPartner) {
        lines.push(event.clientPartner && event.clientPartner.fullName);
      }
      if (event.statusSelect) {
        lines.push(
          `<b>${translate("Status")} :</b> ${status[event.statusSelect - 1]}`
        );
      }
      if (event.qty) {
        if (event.isMaintained === false) {
          lines.push(`<b>${translate("Quantity")} :</b> ${event.qty}`);
        }
      }
      if (event.manufOrder.prioritySelect) {
        if (event.isMaintained === false) {
          let priority = "Faible";
          if (event.manufOrder.prioritySelect === 2) {
            priority = "Normal";
          }
          if (event.manufOrder.prioritySelect === 3) {
            priority = "lev";
          }
          if (event.manufOrder.prioritySelect === 4) {
            priority = "Urgent";
          }
          lines.push(`<b>${translate("Priority")} :</b> ${priority}`);
        }
      }
      if (event.realDuration) {
        lines.push(
          `<b>${translate("Real duration(min)")} :</b> ${event.realDuration /
            60}`
        );
      } else if (event.plannedDuration) {
        lines.push(
          `<b>${translate(
            "Planned duration(min)"
          )} :</b> ${event.plannedDuration / 60}`
        );
      }
      if (event.bom) {
        lines.push(`<b>${translate("BoM")} :</b> ${event.bom}`);
      }
      if (event.prodProcess) {
        lines.push(
          `<b>${translate("Prod. Process")} :</b> ${event.prodProcess}`
        );
      }
      if (event.manufOrder && event.manufOrder.productionOrderSet) {
        let display = "";
        let productionOrders = event.manufOrder.productionOrderSet.map(
          po => po.productionOrderSeq
        );
        if (productionOrders) {
          display = productionOrders.join(" / ");
        }
        lines.push(`<b>${translate("Production order(s)")} :</b> ${display}`);
      }
      if (event.manufOrder && event.manufOrder.saleOrderSet) {
        let display = "";
        let saleOrders = event.manufOrder.saleOrderSet.map(so => so.fullName);
        if (saleOrders) {
          display = saleOrders.join(" / ");
        }
        lines.push(`<b>${translate("Sale order(s)")} :</b> ${display}`);
      }
      if (event.outsourcing) {
        lines.push(`<b>${translate("Outsourcing")} :</b> ${event.outsourcing}`);
      }
      if (event.employees) {
        lines.push("<b>Human Resource List : </b>");
        lines.push(event.employees);
      }
      lines.push(`<b>${translate("From")} : </b> ${long_date_template(start)}`);
      lines.push(`<b>${translate("To")} : </b> ${long_date_template(end)}`);
    }
  }
  return lines.join("<br>");
};

