import { getCookie } from "./utils";
import { trackPromise } from 'react-promise-tracker';

const csrfCookie = getCookie('CSRF-TOKEN');
const headers = new Headers();
headers.append("Accept", "application/json");
headers.append("Content-Type", "application/json");
headers.append("X-Requested-With", "XMLHttpRequest");
headers.append("X-CSRF-Token", csrfCookie);

class RESTService {

  /**
   * In local env
   */
  //static REST_URL = '/kombo-webapp/ws/rest/';
  //static ACTION_URL = '/kombo-webapp/ws/action/';

  /**
   * In production mode
   */
  static REST_URL = '../../ws/rest/';
  static ACTION_URL = '../../ws/action/';


  static fetch(url, options) {

    return trackPromise(fetch(url, options));
  }

  static request(url, config = {}, data = {}) {
    const options = Object.assign(
      {
        method: "POST",
        credentials: "include",
        headers: headers,
        mode: "cors",
        body: JSON.stringify(data)
      },
      config
    );
    if (config.method === "GET") {
      delete options.body;
    }
    return RESTService.fetch(url, options);
  }

  static post(url, data) {
    const config = {
      method: "POST"
    };
    return RESTService.request(url, config, data);
  }

  static put(url, data) {
    const config = {
      method: "PUT"
    };
    return RESTService.request(url, config, data);
  }

  //insert data
  static add(entity, record) {
    const data = {
      data: record
    };
    const url = `${RESTService.REST_URL}${entity}`;
    return RESTService.put(url, data);
  }

  //fetch data
  static getData(entity, data, options = {}) {
    const dataSend = {
      ...data,
      ...options
    };
    const url = `${RESTService.REST_URL}${entity}/search`;
    return RESTService.post(url, dataSend);
  }

  //update
  static update(entity, record, id) {
    const data = {
      data: record
    };
    const url = `${RESTService.REST_URL}${entity}/${id}`;
    return RESTService.post(url, data);
  }

  //delete
  static delete(entity, id) {
    const config = {
      method: "DELETE"
    };
    const url = `${RESTService.REST_URL}${entity}/${id}`;
    return RESTService.request(url, config);
  }

  //fetch single data
  static getId(entity, id) {
    const url = `${RESTService.REST_URL}${entity}/${id}/fetch`;
    return RESTService.post(url, {});
  }

  static getAction(model, action, data) {
    const datas = {
      model: model,
      action: action,
      data: data
    };
    const url = `${RESTService.ACTION_URL}`;
    return RESTService.request(url, {}, datas);
  }
}

export default RESTService;
