import React, { useEffect, useState } from "react";
import Card from "../components/Card";
import { Tooltip } from "@rmwc/tooltip";
import { Radio } from "@rmwc/radio";
import { translate, status, formatDate } from "../services/utils";
import ScrollView from "../components/ScrollView";
import NativeSelect from "../components/NativeSelect";
import Input from "../components/Input/Input";
import LocalStorageService from "../services/LocalStorageService";
import '@rmwc/radio/styles';

const divStyle = {
  height: "450px!important",
  overflow: "scroll"
};

const toolTipStyle = {
  width: "240px",
  height: "auto",
  background: "#ffffff",
  color: "black",
  border: "none",
  borderRadius: "4px",
  fontSize: "1.1em",
  marginLeft: "1%",
  paddingTop: 1,
  paddingLeft: 12,
  paddingBottom: 12,
  boxShadow: "8px 4px 8px 4px rgba(153,153,153,0.35)"
};

const computeTooltipContent = operationOrder => {
  const lines = [];
  lines.push("<div>");
  lines.push(`<b> ${operationOrder.manufOrder &&
    operationOrder.manufOrder.manufOrderSeq} - ${translate("Priority")} : ${
    operationOrder.priority
  }
  </b><br /><br /> ${operationOrder.operationName}`);

  if(operationOrder.realDuration) {
    lines.push(`<b>${translate("Real duration(min)")} :</b> ${operationOrder.realDuration / 60}`);
  }
  else if(operationOrder.plannedDuration) {
    lines.push(`<b>${translate("Planned duration(min)")} :</b> ${operationOrder.plannedDuration / 60}`);
  }

  if (operationOrder.isMaintained === false) {
    if (operationOrder.product) {
      lines.push(operationOrder.product && operationOrder.product.fullName);
    }
  }
  if (operationOrder.clientPartner) {
    lines.push(
      operationOrder.clientPartner && operationOrder.clientPartner.fullName
    );
  }
  if (operationOrder.statusSelect) {
    lines.push(
      `<b>${translate("Status")} :</b> ${
        status[operationOrder.statusSelect - 1]
      }`
    );
  }
  if (operationOrder.qty) {
    if (operationOrder.isMaintained === false) {
      lines.push(`<b>${translate("Quantity")} :</b> ${operationOrder.qty}`);
    }
  }
  if (operationOrder.bom) {
    lines.push(`<b>${translate("BoM")} :</b> ${operationOrder.bom}`);
  }
  if (operationOrder.prodProcess) {
    lines.push(
      `<b>${translate("Prod. Process")} :</b> ${operationOrder.prodProcess}`
    );
  }
  if (operationOrder.manufOrder && operationOrder.manufOrder.productionOrderSet) {
    let display = "";
    let productionOrders = operationOrder.manufOrder.productionOrderSet.map(po => po.productionOrderSeq);
    if(productionOrders) {display = productionOrders.join(" / ");}
    lines.push(
        `<b>${translate("Production order(s)")} :</b> ${display}`
    );
  }
  if (operationOrder.manufOrder && operationOrder.manufOrder.saleOrderSet) {
    let display = "";
    let saleOrders = operationOrder.manufOrder.saleOrderSet.map(so => so.fullName);
    if(saleOrders) {display = saleOrders.join(" / ");}
    lines.push(
        `<b>${translate("Sale order(s)")} :</b> ${display}`
    );
  }
  if (operationOrder.employees) {
    lines.push("<b>Human Resource List : </b>");
    lines.push(operationOrder.employees);
  }
  lines.push(
    `<b>${translate("From")} : </b> ${formatDate(
      operationOrder.plannedStartDateT
    )}`
  );
  lines.push(
    `<b>${translate("To")} : </b> ${formatDate(operationOrder.plannedEndDateT)}`
  );
  lines.push("</div>");
  return lines.join("<br/>");
};

const DraftOperationOrder = ({
  manufacturingOrderList,
  operationOrderListLength,
  draftOperationOrderListLength,
  renderDraftOperationOrder,
  prodHumanResourceList,
  onDragend,
  ...props
}) => {
  const [models, setModels] = useState(null);
  const [filter, setFilter] = useState("");
  const [secondFilter, setSecondFilter] = useState("");

  useEffect(() => {
    let list = [];
    props.workCenterGroupModelList &&
      props.workCenterGroupModelList.forEach(d => {
        list.push({ label: d.name, value: d.id });
      });
    setModels(list);
  }, [props.workCenterGroupModelList]);

  useEffect(() => {
    const timeoutId = setTimeout(function () {
      props.setFilter(filter);
    }, 800);
    return () => clearTimeout(timeoutId);
  }, [filter]);

  useEffect(() => {
    const timeoutId = setTimeout(function () {
      props.setSecondFilter(secondFilter);
    }, 800);
    return () => clearTimeout(timeoutId);
  }, [secondFilter]);

  const onChangeFilter = e => {
    setFilter(e.target.value);
  };

  const onChangeSecondFilter = e => {
    setSecondFilter(e.target.value);

  };

  const onChangeWorkCenterGroup = value => {
    if(models) {
      const v = !value ? null : models.find(w => w.value === value.value);
      props.setWorkCenterGroup(v);
    }
  };

  const onDragStart = e => {
    e.dataTransfer.effectAllowed = "move";
    LocalStorageService.setDragDataId(e.target.getAttribute("data-id"));
  };
  
  const onChangeSearchTypeSelect = (evt) => {
    props.setSearchTypeSelect(String(evt.currentTarget.value));
  };

  const cardList = renderDraftOperationOrder.map(o => {
    const tooltipContent = computeTooltipContent(o);
    let css = "";
    if (o.saleOrder && o.saleOrder.sdsTypeSelect === 2 ) {
      css = " event_saleOrder_not_posed";
    }
    if (o.saleOrder && o.saleOrder.sdsTypeSelect === 1 ) {
      css = " event_saleOrder_posed";
    }
    return (
      <div
        onDragEnd={onDragend}
        draggable="true"
        onDragStart={onDragStart}
        id={`draftOperationOrder-${o.id}`}
        data-id={o.id}
        key={o.id}
      >
        <Tooltip
          showArrow={true}
          activateOn="click"
          content={
            <div
              style={toolTipStyle}
              dangerouslySetInnerHTML={{ __html: tooltipContent }}
            ></div>
          }
        >
          <Card>
            <b>
              <div style={{
                backgroundColor: o.headerColor,
                width: "100%",
                height: "100%",
                color: o.textColor
              }}>
                <div style={{ display: "flex" }}>
                  {o.manufOrder && o.manufOrder.manufOrderSeq}
                  <div style={{ position: "absolute", top: 0, right: "2px" }}>
                    {o.materialCode ? o.materialCode.sdsCode : ''}
                  </div>
                </div>
              </div>
            </b>
            <div
              className={css}
              style={{
                backgroundColor: o.contentColor,
                width: "100%",
                height: "100%",
                color: o.textColor
              }}
            >
              <div className="ellipsive_content">
                {o.operationName}
                <br />
                {o.product && o.product.fullName}
                <br />
                {o.clientPartner && o.clientPartner.fullName}
                <br />
              </div>
            </div>
          </Card>
        </Tooltip>
      </div>
    );
  });

  const draftTitle = translate("Operations order to plan");
  const filterTitle = translate("Filter on");
  const left = translate("Left");
  const right = translate("Right");
  const all = translate("All");
  return (
    <React.Fragment>
      <h3 style={{ textAlign: "center", color: "#0288d1" }}>
        {draftTitle}
      </h3>
      <h5 style={{ textAlign: "center", color: "#0288d1", marginBottom: 0 }}>
        {filterTitle}
      </h5>
      <Radio
          value="left"
          checked={props.searchTypeSelect === 'left'}
          onChange={onChangeSearchTypeSelect}
      >
        {left}
      </Radio>

      <Radio
          value="right"
          checked={props.searchTypeSelect === 'right'}
          onChange={onChangeSearchTypeSelect}
      >
        {right}
      </Radio>
      <Radio
          value="all"
          checked={props.searchTypeSelect === 'all'}
          onChange={onChangeSearchTypeSelect}
      >
        {all}
      </Radio>
      <Input
        label={translate("Filter (product, customer, ...)")}
        type="text"
        name="filter"
        value={filter}
        onChange={onChangeFilter}
      />
      <Input
          label={translate("Filter (product, customer, ...)")}
          type="text"
          name="secondFilter"
          value={secondFilter}
          onChange={onChangeSecondFilter}
      />
      {
        models && models.length > 0 && (
            <NativeSelect
                name="filterByWorkCenterGroup"
                label={translate("Filter by work center group")}
                value={props.workCenterGroup}
                options={models}
                onChange={onChangeWorkCenterGroup}
                canRemove={true}
            />
        )
      }

      <ScrollView style={{ height: "100%" }}>
        <div style={divStyle}>
          {cardList}
        </div>
      </ScrollView>
    </React.Fragment>
  );
};

export default DraftOperationOrder;
