import React from "react";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import { withStyles } from "@material-ui/styles";
import Button from "@material-ui/core/Button";
import Input from "./Input/Input";
import { translate } from "../services/utils";

const styles = {
  dialogPaper: {
    minWidth: "50%",
    maxHeight: "40%"
  }
};

const ChangeDurationDialog = props => {
  const onChangeReason = e => {
    props.setReason(e.target.value);
  };

  const onValidate = () => {
    props.setChangeDurationOpen(false);
    props.validateChangeDuration();
  };

  const handleClose = () => {
    props.setChangeDurationOpen(false);
  };

  const validate = translate("Validate");
  const error = translate("The reason is mandatory");
  return (
    <Dialog
      aria-labelledby="simple-dialog-title"
      open={props.open}
      classes={{ paper: props.classes.dialogPaper }}
      onClose={handleClose}
    >
      <>
        <DialogTitle id="simple-dialog-title">{translate("Change duration")}</DialogTitle>
        <Input
          label={translate("Reason")}
          type="text"
          name="reason"
          value={props.reason}
          onChange={onChangeReason}
        />
        {!props.reason && (
          <>
            <div style={{ color: "red", textAlign: "center" }}>{error}</div>
            <Button readOnly={!props.reason} onClick={onValidate} variant="contained" color="primary" disabled>
              {validate}
            </Button>
          </>
        )}
        {props.reason && (
            <Button readOnly={!props.reason} onClick={onValidate} variant="contained" color="primary">
              {validate}
            </Button>
        )}


      </>
    </Dialog>
  );
};

export default withStyles(styles)(ChangeDurationDialog);
